
.zion-default-btn,
#zion-submit-btn {
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  --arrow-hover-move-x: -110%;
  gap: 15px;
  z-index: 1;
  position: relative;
  color: var(--heading-color);
  border: 1px solid var(--heading-color);
  @include max (767px){
    padding: 18px 30px;
  }
  svg path{
    transition: all 0.4s;
  }
   &:hover{
    color: var(--accent-color);
    border: 1px solid var(--accent-color);
    svg path{
      fill: var(--accent-color);
    }
  }
  &.zion-header-btn{
    border: none;
    padding: 0;
    @include max (767px){
      display: none;
    }
    &:hover{
      color: var(--accent-color);
      svg path{
        fill: var(--accent-color);
      }
      &::before{
        bottom: -2px;
        left: 0;
        width: 95px;
        height: 2px;
        transition: all 0.4s;
        background-color: var(--accent-color);
      }
    }
    &::before{
      content: "";
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 95px;
      height: 2px;
      background-color: var(--heading-color);
    }
  }
  &.d-block {
    width: 100%;
  }
  &:focus {
    box-shadow: none;
  }
}

.zion-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}



// add animation


.zion-default-btn svg {
  transition: all 0.3s ease-out 0s;
}
.zion-default-btn:hover svg path:nth-of-type(1), .zion-default-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.zion-default-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.zion-default-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.zion-default-btn:hover svg path:nth-of-type(2), .zion-default-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.zion-default-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.zion-default-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.zion-default-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
