.zion-footer{
  &-section{
    padding: 375px 0 0;
    position: relative;
    overflow: hidden;
    z-index: 0;
    margin-top: -275px;
    @include max (991px){
      padding: 340px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
      margin-top: 40px;
    }
  }
  &-menu{
    h5{
      margin-bottom: 24px;
     }
    ul{
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--heading-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
  
  }
  &-menu2{
    padding-left: 150px;
    @include max (1199px){
      padding-left: 100px;
    }
    @include max (991px){
      padding-left: 0px;
    }
    @include max (767px){
      padding-top: 40px;
    }
    h5{
      margin-bottom: 24px;
     }
    ul{
      li{
        margin-bottom: 15px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--heading-color);
        }
      }
    }
  
  }
  &-textarea{
    padding-right: 30px;
    @include max (1399px){
      margin-bottom: 30px;
      max-width: 500px;
    }
    img{
      padding-bottom: 24px;
    }
    p{
      margin-bottom: 24px;
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-menu-wrap {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &-bottom-text{
    margin-top: 80px;
    border-top: 1px solid #181B0E1A;
    padding: 30px 0;
    text-align: center;
    @include max (767px){
      margin-top: 40px;
    }
    p{
      font-size: 18px;
      line-height: 25px;
    }
    &.text2{
      p{
        color: var(--light-color);
      }
    }
  }
}
.pl-30{
  padding-left: 30px;
  @include max (1399px){
    padding-left: 50px;
  }
  @include max (1199px){
    padding-left: 0;
    padding-top: 40px;
  }
}
.pl-40{
  padding-left: 40px;
  @include max (1399px){
    padding-left: 70px;
  }
  @include max (1199px){
    padding-left: 0;
  }
  @include max (767px){
    padding-top: 40px;
  }

}
.pl-100{
  padding-left: 100px;
  @include max (1399px){
    padding-left: 0;
  }
  @include max (1199px){
    padding-left: 0;
  }
  
}
