.zion-p{
    &-wrap{}
    &-thumb{
        img{
            border-radius: 10px;
            margin-bottom: 24px;
        }
    }
    &-meta{
        ul{
            li{
                display: inline-block;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    font-size: 18px;
                    line-height: 1.44em;
                    color: var(--heading-color);
                }
            }
        }
    }
    &-wrap{}
}