.zion-team{
    &-wrap{
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            .zion-team{
                &-thumb img{
                    transform: scale(1.1);
                }
            }
            .zion-team-social{
                bottom: 0;
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        z-index: 0;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    &-social{
        position: absolute;
        bottom: -88px;
        left: 0;
        transition: all 0.4s;
    }
    &-content{
        padding-top: 24px;
        @include max (1199px){
            padding-top: 15px;
        }
        p{
           
            color: var(--heading-color);
        }
    } 
   
    // add single
    &-d-thumb{
        @include max (991px){
            text-align: center;
            padding-bottom: 40px;
        }
    }
    &-d-content-wrap{
        display: flex;
        align-items: center;
        gap: 24px;
    }
    &-d-content{
        padding-top: 100px;
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 40px;
        }
        h4{
            padding-bottom: 24px;
        }
        ul{
            li{
                font-weight: 400;
                padding-bottom: 15px;
                padding-left: 20px;
                position: relative;
                &:last-child{
                    padding-bottom: 0;
                }
                span{
                    font-weight: 500;
                }
                &::before{
                    width: 8px;
                    height: 8px;
                    top: 8px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
}
.team-btn{
    text-align: center;
    @include max (767px){
        margin-top: 0!important;
    }
}