.zion-t{
    &-wrap{
         display: grid!important;
         gap: 135px;
         align-items: center;
         grid-template-columns: 416px 1fr;
        @include max (1199px){
            gap: 10%;
        }
        @include max (991px){
            display: block!important;
        }
    }
    &-thumb{
        flex-shrink: 0;
        img{
            border-radius: 10px;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-author-title{
        padding-bottom: 24px;
        border-bottom: 1px solid #181B0E1A;
        margin-bottom: 24px;
        max-width: 305px;
        span{
            font-size: 21px;
            font-weight: 600;
            line-height: 30px;
            padding-right: 5px;
        }
        p{
            font-size: 21px;
            line-height: 30px;
        }
    }
}

.zion-t-slider-init .zion-t-slider-next, 
.zion-t-slider-init .zion-t-slider-prev {
    bottom: 0;
    right: 80px;
    width: 60px;
    height: 60px;
    z-index: 1;
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--light-color);
    border: 2px solid var(--heading-color);
    background-image: url(../images/testimonial/left-arrow.svg);
    @include max (767px){
        display: none!important;
    }
}
.zion-t-slider-init .zion-t-slider-prev {
    left: auto;
    right: 0;
    background-image: url(../images/testimonial/right-arrow.svg);
   
}
