.zion-service-content{
    &-wrap{
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: all 0.4s;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 24px;
        &.item2{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        &:hover{
            .zion-service-content-title h3{
                color: var(--accent-color);
            }
            .zion-service-content-icon svg path{
                fill: var(--accent-color);
            }
        }
    }
    &-title{
        h3{
            color: var(--light-color1);
            transition: all 0.4s;
        }
    }
    &-icon{
        svg path{
            transition: all 0.4s;
        }
    }
    // &-thumb{
    //     opacity: 0;
    //     img{
    //         border-radius: 10px;
    //     }
    // }
}

.light-color{
    color: var(--light-color1);
}