// counter

.zion-counter{
  &-wrap{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: space-between;
    padding-bottom: 130px;
     @include max (991px){
        padding-bottom: 100px;
      }
    @include max (767px){
      padding-bottom: 80px;
    }
  }
  &-item {
    p{
      font-size: 28px;
      line-height: 1.21em;
      padding-top: 15px;
    }

  }
  &-data {
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    height: 80px;
    @include max (991px){
      height: 65px;
    }
    @include max (767px){
      height: 50px;
    }
    
  }
  &-thumb{
    img{
      width: 100%;
    }
    text-align: center;
    padding: 0 24px;
    @include max(1399px) {
      padding-left: 0;
    }
    @include max (991px){
      padding: 0 0 40px;
      text-align: left;
    }
    img{
      border-radius: 20px;
      
    }
  }
}


.zion-counter-data > span {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 2s ease;
  transform: translateY(0);
  line-height: 1;
  
}
.data2 > span{
  width: 15%;
  @include max (1199px){
    width: 10%;
  }
}
.zion-counter-data > span span {
  flex: 0 0 100%;
  height: 100%;
}
.zion-counter-data:nth-child(1) > span {
  transition-delay: 0s;
}
.zion-counter-data:nth-child(2) > span {
  transition-delay: 0.375s;
}
.zion-counter-data:nth-child(3) > span {
  transition-delay: 0.75s;
}
.zion-counter-data:nth-child(4) > span {
  transition-delay: 1.125s;
}
.zion-counter-data:nth-child(5) > span {
  transition-delay: 1.5s;
}
.zion-counter-data:nth-child(6) > span {
  transition-delay: 1.875s;
}
.zion-counter-data:nth-child(7) > span {
  transition-delay: 2.25s;
}
.zion-counter-data:nth-child(8) > span {
  transition-delay: 2.625s;
}
.zion-counter-data:nth-child(9) > span {
  transition-delay: 3s;
}
.zion-counter-data:nth-child(10) > span {
  transition-delay: 3.375s;
}
.zion-counter-data:nth-child(11) > span {
  transition-delay: 3.75s;
}
.zion-counter-data:nth-child(12) > span {
  transition-delay: 4.125s;
}
.zion-counter-data:nth-child(13) > span {
  transition-delay: 4.5s;
}
.zion-counter-data:nth-child(14) > span {
  transition-delay: 4.875s;
}
.zion-counter-data:nth-child(15) > span {
  transition-delay: 5.25s;
}
.zion-counter-data:nth-child(16) > span {
  transition-delay: 5.625s;
}
.zion-counter-data:nth-child(17) > span {
  transition-delay: 6s;
}
.zion-counter-data:nth-child(18) > span {
  transition-delay: 6.375s;
}
.zion-counter-data:nth-child(19) > span {
  transition-delay: 6.75s;
}
.zion-counter-data:nth-child(20) > span {
  transition-delay: 7.125s;
}


.counter-card{
  h2{
    color: var(--white-color);
    font-size: 40px;
    line-height: 48px;
    height: 40px;
  }
  span.text{
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    padding-right: 12px;
    color: var(--white-color);
  }
}