.zion-hero{
  &-content{
    padding: 210px 0 0;
    @include max (1199px){
      padding: 190px 0 0;
    }
    @include max (991px){
      padding: 170px 0 0;
    }
    @include max (767px){
      padding: 150px 0 0;
    }
  }
  &-thumb{
    padding: 80px 0 60px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    img{
      border-radius: 10px;
    }
    @include max (991px){
    padding: 60 0 40px 0;
    grid-template-columns: repeat(2, 1fr);
    }
    @include max (767px){
      padding: 40px 0 30px 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }
 
  // end one
  
}
.zion-sub-title{
  padding: 10px 20px;
  border-radius: 50px;
  display: inline-block;
  margin-bottom: 24px;
  background-color: rgba(29, 29, 27, 0.05);
  p{
    font-weight: 700;
    font-size: 18px!important;
    line-height: 1.33em!important;
    text-transform: uppercase;
    @include max (767px){
      font-weight: 500;
    }
  }
  &.title2{
    background-color: rgba(255, 255, 255, 0.05);
    p{
      color: var(--light-color1);
    }
  }
}

.zion-deafault-bg{
  margin: 0 30px 30px;
  border-radius: 10px;
  background-color: var(--light-bg);
  @include max (991px){
    margin: 0 20px 20px;
  }
  @include max (767px){
    margin: 0 10px 10px;
  }
}